.projects{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #b564ff;
}

.myProject{
    display: flex;
    border: 1px solid white;
    min-height: 80%;
    min-width: 65%;
    justify-content: center;
    flex-wrap: wrap;
}

.inner{
    display: flex;
    border: 1px solid yellow;
    min-width: 100%;
    min-height: 400px;
    justify-content: space-between;
    flex-wrap: wrap;
}

.Content{
    display: flex;
    border: 1px solid purple;
}

